import React from 'react';
import {ProgressBar} from "../../../widgets/ProgressBar";
import { Typography } from 'antd';
const { Title } = Typography;

const ZerothPage = () => {
    return (
        <div className={'Page'}>
            <ProgressBar percent={0}/>
            <div>
                <Title>
                    <p>,       ?</p>
                    <p>  ?</p>
                    <p>  ,  !</p>
                </Title>
            </div>
        </div>
    );
};

export default ZerothPage;