import React from 'react';
import {ProgressBar} from "../../../widgets/ProgressBar";
import {Typography} from 'antd';
const {Title, Paragraph} = Typography;

const SeventhPage = () => {
    return (
        <div className={'Page'}>
            <ProgressBar percent={85}/>
            <div>
                <Title level={3}>,   !</Title>
                <Title level={3}>   .</Title>
                <Title level={3}>   :</Title>
                <Paragraph copyable>P6H3K-A4ALR-0X4B8</Paragraph>
                <Title level={4}>        - </Title>
                <Title level={4}>  ,      - </Title>
                <Title level={4}>     ,      - </Title>
            </div>
        </div>
    );
};

export default SeventhPage;